import './style.css'
import * as THREE from 'three'
import * as dat from 'lil-gui'
import gsap from 'gsap'

import cv from '../static/AntinoKilaCV.pdf'
console.log(cv)

import vertex from './shaders/vertex.glsl'
import fragment from './shaders/fragment.glsl'
import pointsFragment from './shaders/pointsFragment.glsl'
import pointsVertex from './shaders/pointsVertex.glsl'
import linesFragment from './shaders/linesFragment.glsl'
import aboutVertex from './shaders/aboutVertex.glsl'
import aboutFragment from './shaders/aboutFragment.glsl'
import backVertex from './shaders/backVertex.glsl'
import backFragment from './shaders/backFragment.glsl'
import projectsVertex from './shaders/projectsVertex.glsl'
import projectsFragment from './shaders/projectsFragment.glsl'



// Debug
const settings = {
    time:0,
    pointsStep1 : 0,
    pointsStep2 : 0
}

// const gui = new dat.GUI()

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

/**
 * Sizes
 */
 const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}



/**
 * Loading screen
 */

const loadPage = ()=>{
    const loadingScreen = document.querySelector('.page-loading')
    const loadingScreenValue = document.querySelector('.loading-progress')
    let loadingValue = 0
    let loadingEndValue = 100
    
    const loadingInterval = window.setInterval(()=>{
    
        loadingValue++
        loadingScreenValue.style.transform = `scaleX(${loadingValue / 100})`
    
        if(loadingValue == loadingEndValue){
            loadingScreen.classList.add('page-loading-close')
            gsap.to('#section1-title' , {duration:1 , y:0 , ease: "elastic.out(1, 0.3)"})
            gsap.to('#section1-subtitle' , {duration:1 , y:0 , ease: "elastic.out(1, 0.3)"})
            gsap.to('#section1-info' , {duration:1 , y:0 , ease: "elastic.out(1, 0.3)"})
            window.clearInterval(loadingInterval)
        }
    },1)
}
loadPage()
const checkDevice = ()=>{
    if(sizes.width <= 420){
        camera.position.z = 6
    }
}

/**
 * Textures
 */
const loadingManager = new THREE.LoadingManager(()=>{
})
const textureLoader = new THREE.TextureLoader(loadingManager)
const aboutTexture = textureLoader.load('/textures/aboutme.png')

/**
 * 3d Objects
 */
// Geometries
const backgroundGeometry = new THREE.PlaneBufferGeometry(sizes.width / 10, sizes.height / 10)
const geometry = new THREE.IcosahedronBufferGeometry(1, 128 , 128)
const pointsGeometry = new THREE.IcosahedronBufferGeometry(1 , 32 , 64)
const linesGeometry = new THREE.SphereBufferGeometry( 1 , 64 , 32 );

// Materials
const material = new THREE.ShaderMaterial({
    vertexShader:vertex,
    fragmentShader:fragment,
    uniforms:{
        uTime : { value: 0 },
        uColorMultiplier1: { value:1 },
        uColorMultiplier2 :{ value:1 },
        uSize : { value:20 }
    }
})
const pointsMaterial = new THREE.ShaderMaterial({
    vertexShader: pointsVertex,
    fragmentShader: pointsFragment,
    uniforms:{
        uTime : { value: 0 },
        uPointsStep1 : { value:0.3},
        uPointsStep2 : { value: 0.5},
        uColor : { value: new THREE.Vector3(1,1,1)}
    }
})
const linesMaterial = new THREE.ShaderMaterial({
    transparent: true,
    vertexShader:vertex,
    fragmentShader: linesFragment,
    uniforms:{
        uTime : { value: 0 },
        uPointsStep1 : { value:0.3},
        uPointsStep2 : { value: 0.5},
        uColor : { value: new THREE.Vector3(1,1,1)}
    }
})

// Meshes
const meshesArray =[]

const backgroundMesh = new THREE.Mesh(
    backgroundGeometry ,
    new THREE.ShaderMaterial({
        transparent: true,
        vertexShader:backVertex,
        fragmentShader: backFragment,
        uniforms:{
            uTime : { value: 0 },
            uTexture : {value: textureLoader.load('textures/2.jpg')},
            uColor:{ value: new THREE.Vector3(0.02,0.02,0.02) }
        }
    })
)
backgroundMesh.position.z = -3

const mesh = new THREE.Mesh(geometry, material)

const pointsMesh = new THREE.Points(pointsGeometry , pointsMaterial)
pointsMesh.scale.set(1.5,1.5,1.5)

const pointsMesh2 = new THREE.Points(pointsGeometry , material)
pointsMesh2.scale.set(1.5,1.5,1.5)

const lines = new THREE.LineSegments(linesGeometry,linesMaterial)
lines.scale.set(1.2,1.2,1.2)

const aboutMesh = new THREE.Mesh( 
    new THREE.PlaneBufferGeometry(3,3,1) ,
    new THREE.ShaderMaterial({
        side: THREE.DoubleSide,
        transparent: true,
        vertexShader:aboutVertex,
        fragmentShader:aboutFragment,
        uniforms:{
            uTime : { value: 0 },
            uTexture : { value: aboutTexture}
        }
    })
)
aboutMesh.visible = false
aboutMesh.position.set(9,0,0)
aboutMesh.rotation.y = 0.8
aboutMesh.scale.x = 1.5

const projectsMesh = new THREE.Mesh(
    new THREE.PlaneBufferGeometry(4,3,1),
    new THREE.ShaderMaterial({
        side: THREE.DoubleSide,
        vertexShader:projectsVertex,
        fragmentShader:projectsFragment,
        uniforms:{
            uTime:{value:0},
            uTexture: {value:null}
        }
    })
)
projectsMesh.visible = false

scene.add(mesh , pointsMesh , pointsMesh2 , lines , aboutMesh , backgroundMesh ,projectsMesh)
meshesArray.push(mesh,pointsMesh,pointsMesh2 , lines)


/**
 * Window and mouse events
 */

// Resizing window
window.addEventListener('resize', () =>{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

// Scrolling events
let scrollY = 0

window.addEventListener('scroll' , ()=>{
    scrollY = window.scrollY / sizes.height

    // Gsap animations on scroll
    if(scrollY >=0 && scrollY < 0.5){
        gsap.to(camera.position ,{duration:1,z:3})
        gsap.to(camera.position ,{duration:1,x:0})
        gsap.to(camera.position ,{duration:1,y:0})
        for(const mesh of meshesArray){
            gsap.to(mesh.position , {duration:1,x:0})
        }
    }
    else if(scrollY > 0.5 && scrollY <1.5){
        if(projectsMesh.visible){
            projectsMesh.visible = false
        }
        gsap.to(camera.position ,{duration:1,z:5})
        gsap.to(camera.position ,{duration:1,x:0})
        gsap.to(camera.position ,{duration:1,y:0})
        gsap.to(camera.rotation , {duration:1 , z:0})
        for(const mesh of meshesArray){
            gsap.to(mesh.position , {duration:1,x:3})
        }

        pointsMaterial.uniforms.uColor.value = new THREE.Vector3(1,1,1)

        if(scrollY > 0.8){
            gsap.to('.section2-content' , {duration:1 , y:0 , ease: "elastic.out(1, 0.3)"})
        }
    }
    else if(scrollY >1.5 && scrollY < 2.5 ) {
        gsap.to(camera.position , {duration:1 , z:5})
        gsap.to(camera.position , {duration:1 , x:-2})
        gsap.to(camera.position , {duration:1 , y:0})
        gsap.to(camera.rotation , {duration:1 , z:-2})
        gsap.to(mesh.position , {duration:1 , x:-4})
        gsap.to(pointsMesh.position , {duration:1 , x:-1.5})
        gsap.to(pointsMesh2.position , {duration:1 , x:5})
        pointsMaterial.uniforms.uColor.value = new THREE.Vector3(1,1,1)

        gsap.to('.section3-title' , {duration:1 , y:0 , ease: "elastic.out(1, 0.3)"})
        gsap.to('.section3-text' , {duration:1.2 , y:0 ,ease: "elastic.out(1, 0.3)"})
        gsap.to('.section3-projects' , {duration:1.4, y:0 ,ease: "elastic.out(1, 0.3)"})

        for(const mesh of meshesArray){
            mesh.visible = true
        }

        gsap.to(backgroundMesh.material.uniforms.uColor.value , {duration:1 , x:0.02})
        gsap.to(backgroundMesh.material.uniforms.uColor.value , {duration:1 , y:0.02})
        gsap.to(backgroundMesh.material.uniforms.uColor.value , {duration:1 , z:0.02})
    }
    else if(scrollY >2.5 && scrollY < 3.5 ) {
        if(projectsMesh.visible){
            projectsMesh.visible = false
        }
        gsap.to(camera.position , {duration:1 , z:3})
        gsap.to(camera.rotation , {duration:1 , z:-5})
        gsap.to(mesh.position , {duration:1 , x:-8})
        gsap.to(pointsMesh.position , {duration:1 , x:-1.5})
        gsap.to(pointsMesh2.position , {duration:1 , x:5})
    }
    else if(scrollY >3.5 && scrollY < 5 ) {
        gsap.to(camera.position ,{duration:1,z:5})
        gsap.to(camera.rotation , {duration:1 , z:0})
        gsap.to(camera.position ,{duration:1,x:0})
        gsap.to(camera.position ,{duration:1,y:0})

        for(const mesh of meshesArray){
            mesh.visible = true
            gsap.to(mesh.position , {duration:1,x:0})
        }

        gsap.to(backgroundMesh.material.uniforms.uColor.value , {duration:1 , x:0.02})
        gsap.to(backgroundMesh.material.uniforms.uColor.value , {duration:1 , y:0.02})
        gsap.to(backgroundMesh.material.uniforms.uColor.value , {duration:1 , z:0.02})
    }

    // Progress Bar scrolling
    progressBarScroll(window.scrollY)
})

//Mouse move events
let cursor = {
    x:0,
    y:0
}
window.addEventListener('mousemove' , (e)=>{
    cursor.x = ( e.clientX / sizes.width  )
    cursor.y = (e.clientY / sizes.height ) 
    
    // Changing vertex color on mouse movement
    material.uniforms.uColorMultiplier1.value = cursor.x
    material.uniforms.uColorMultiplier2.value = cursor.y

    material.uniforms.uSize.value = cursor.x + 10
    material.uniforms.uSize.value = cursor.y + 10
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.set(0, 0 , 3)
scene.add(camera)


/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas,
})
renderer.setClearColor(0x111111)
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))



/**
 * Animate
 */
const clock = new THREE.Clock()

const tick = () =>{

    const elapsedTime = clock.getElapsedTime()

    // Update shader time
    settings.time = elapsedTime 
    mesh.material.uniforms.uTime.value = settings.time * 0.2
    pointsMesh.material.uniforms.uTime.value = settings.time * 0.2
    lines.material.uniforms.uTime.value = settings.time * 0.3
    aboutMesh.material.uniforms.uTime.value = settings.time * 0.1
    backgroundMesh.material.uniforms.uTime.value = settings.time * 0.5
    projectsMesh.material.uniforms.uTime.value = settings.time * 0.5

    // Meshes animation
    mesh.rotation.x = Math.PI * 2 * elapsedTime * 0.02
    pointsMesh.rotation.y = Math.PI * 2 * elapsedTime * 0.02
    lines.rotation.z = Math.sin(elapsedTime * Math.PI) * 0.1
    lines.rotation.y = Math.cos(elapsedTime * Math.PI) * 0.1


    // Render
    renderer.render(scene, camera)
    // effectComposer.render()

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)

    checkDevice()
}
tick()


/**
 * Javascript DOM
 */

// CV button
const cvBtn = document.querySelector('.cv-btn')
cvBtn.addEventListener('click' , ()=>{
    window.location.href = cv
})

//Progress bar
const progressTitles = document.querySelectorAll('.progress-titles')
const progressBar = document.querySelector('.progress-bar')

progressTitles.forEach((p)=>{
    p.addEventListener('click',(e)=>{
        if(p.id == 'intro'){
            window.scrollTo({
                top:0,
                left:0,
                behavior: 'smooth'
            })
        }
        else if(p.id == 'skills'){
            window.scrollTo({
                top:sizes.height,
                left:0,
                behavior: 'smooth'
            })
        }
        else if(p.id == 'projects'){
            window.scrollTo({
                top:sizes.height*2,
                left:0,
                behavior: 'smooth'
            })
        }
        else if(p.id == 'contact'){
            window.scrollTo({
                top:sizes.height*3,
                left:0,
                behavior: 'smooth'
            })
        }
        else {
            window.scrollTo({
                top:sizes.height*4,
                left:0,
                behavior: 'smooth'
            })
        }
    })
})
const progressBarColor = (index)=>{
    for(let i=0; i<progressTitles.length; i++){
        if(i===index){
            progressTitles[index].style.color = '#758aff'
            progressTitles[index].style.fontSize = '1.3rem'
        }
        else {
            progressTitles[i].style.color = 'white'
            progressTitles[i].style.fontSize = '1.1rem'
        }
    }
}
const progressBarScroll = (scroll)=>{
    if (scroll>=0 && scroll<sizes.height){
        progressBarColor(0)
    }
    else if (scroll>=sizes.height && scroll<sizes.height*2){
        progressBarColor(1)
    }
    else if (scroll>=sizes.height*2 && scroll<sizes.height*3){
        progressBarColor(2)
    }
    else if (scroll>=sizes.height*3 && scroll<sizes.height*4){
        progressBarColor(3)
    }
    else if (scroll>=sizes.height*4 && scroll<sizes.height*5){
        progressBarColor(4)
    }
}


// About page
const aboutBtn = document.querySelector('.about-btn')
const closeAboutBtn = document.querySelector('.close-about-btn')
const about = document.querySelector('.about')
const section1 = document.querySelector('.section1')

closeAboutBtn.addEventListener('click' , ()=>{
    aboutPageOpen( false , 0 , 0)
})
aboutBtn.addEventListener('click' , ()=>{
    aboutPageOpen(true , 9 , -sizes.width)
})

const aboutPageOpen = ( state , xValue , transformLinks)=>{
    document.body.classList.toggle('body-noscroll')
    aboutMesh.visible = state
    about.classList.toggle('about-show')
    section1.classList.toggle('section1-hide')
    gsap.to(camera.position ,{duration:1, x:xValue , ease: "elastic.out(1, 0.3)"})
    gsap.to(camera.position ,{duration:1, y:0 , ease: "elastic.out(1, 0.3)"})
    gsap.to(camera.position ,{duration:1, z:3 , ease: "elastic.out(1, 0.3)"})
    gsap.to('.socials' ,{duration:1.2 , x:transformLinks , ease: "elastic.out(1, 0.3)"})
    gsap.to('.navbar' ,{duration:1.4, x:transformLinks , ease: "elastic.out(1, 0.3)"})
    gsap.to('.progress-bar' ,{duration:1.6, x:transformLinks , ease: "elastic.out(1, 0.3)"})
}

aboutBtn.addEventListener('mouseover' , (e)=>{
    gsap.to(e.currentTarget , {duration:0.5 , y:2 , ease: "elastic.out(1, 0.3)"})
})
aboutBtn.addEventListener('mouseout' , (e)=>{
    gsap.to(e.currentTarget , {duration:0.5 , y:0 , ease: "elastic.out(1, 0.3)"})
})


