// Create projects images & close projects
const projectMeshesArray = []
const createProjectMesh = (index , t ,positionX)=>{

    document.body.style.overflow = 'hidden'
    progressBar.style.display = 'none'
    projectsMesh.visible = false
    projectsWrapper.classList.add('section3-content-hide')
    projectDivs[index].classList.add('project-divs-open')

    gsap.to(camera.position , {duration:1 , z:6})
    gsap.to(camera.rotation ,{ duration:1 , y:0})
    gsap.to(camera.rotation ,{ duration:1 , x:0})
    gsap.to(camera.rotation ,{ duration:1 , z:0})

    const mesh = new THREE.Mesh(
        new THREE.PlaneBufferGeometry(4,3,1),
        new THREE.ShaderMaterial({
            side: THREE.DoubleSide,
            vertexShader:projectsVertex,
            fragmentShader:projectsFragment,
            uniforms:{
                uTime:{value:0},
                uTexture: {value:t}
            }
        })
    )
    mesh.position.x = positionX
    mesh.scale.set(1.5,1.5,1.5)
    scene.add(mesh)
    projectMeshesArray.push(mesh)
}
const closeProjects = (index)=>{
    document.body.style.overflow = 'auto'
    progressBar.style.display = 'flex'

    for(const mesh of projectMeshesArray){
        mesh.geometry.dispose();
        mesh.material.dispose();
        scene.remove( mesh );
    }

    projectsWrapper.classList.remove('section3-content-hide')
    projectDivs[index].classList.remove('project-divs-open')
}
